import React, { useState, useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/styles';
import { useSelector } from 'react-redux';
import {
  WETH_ADDRESS,
  ETH_ADDRESS,
  CHART_TICK_COUNT
} from 'utils/constants';
import useStyles from './style';
import { NormalCard } from 'components';
import {
  AreaChart,
  Area,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';
import { Grid, List, ListItem, Box, Popover, Card } from '@material-ui/core';
import { InfoIcon } from 'assets/svgs/icon';
import moment from 'moment';
import util from 'API-Service/util';
import { getCorrectDecValue } from 'web3/contracts';
import { CustomizedActiveDot } from 'components/Chart';
import { ArrowDropDown, ArrowDropUp } from '@material-ui/icons';

const timeKey = [
  {title: '1H', time: 3600},
  {title: '1D', time: 3600 * 24},
  {title: '1W', time: 3600 * 24 * 7},
  {title: '1M', time: 3600 * 24 * 30},
  {title: '1Y', time: 3600 * 24 * 365},
  {title: 'ALL', time: 3600 * 24 * 365 * 10}
]

const Portfolio = props => {
  const { tokenList, setTimeIndex, timeIndex, blockTime } = props;
  const theme = useTheme();
  const classes = useStyles(theme);
  const [data, setData] = useState([]);
  const [currentPrice, setCurrentPrice] = useState(0);
  const [changePrice, setChangePrice] = useState(0);
  const [change, setChange] = useState(0);
  const [minPrice, setMinPrice] = useState(0);
  const [maxPrice, setMaxPrice] = useState(50000);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open = Boolean(anchorEl);

  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  useEffect(() => {
    if (tokenList.length > 0) {
      getTokenPrice()
    }
  }, [tokenList])

  const getTokenPrice = React.useCallback(async () => {
    const tokens = tokenList.map((item, index) => {return item.address === ETH_ADDRESS ? WETH_ADDRESS : item.address});
    let _tokenList = JSON.stringify(tokens);
    _tokenList = _tokenList.replace("\'", "\"");
    const _res = await util.getTokenListPriceInfo(_tokenList, blockTime - timeKey[timeIndex].time)
    const tokenPriceInfo = _res.data.tokenPricesData;
    let finalTokenInfoList = tokenPriceInfo.map((item, index) => {
      return {
        ...item,
        ...tokenList[index],
      }
    });

    let finalData = []
    for (let i = 0; i < finalTokenInfoList.length; i ++) {
      finalTokenInfoList[i].graphInfo = sortArray(blockTime, finalTokenInfoList[i])
    }

    for (let i = 0; i < CHART_TICK_COUNT; i ++) {
      finalData[i] = {balance: 0}
      for (let j = 0; j < finalTokenInfoList.length; j ++) {
        finalData[i].balance += Number(Number(getCorrectDecValue(finalTokenInfoList[j].graphInfo[CHART_TICK_COUNT - i - 1].price * finalTokenInfoList[j].graphInfo[CHART_TICK_COUNT - i - 1].balance, finalTokenInfoList[j].graphInfo[CHART_TICK_COUNT - i - 1].decimals, -1)).toFixed(2))
        finalData[i].transactionTime = finalTokenInfoList[j].graphInfo[CHART_TICK_COUNT - i - 1].transactionTime
      }
    }
    finalData = finalData.sort((a,b) => {return a.transactionTime - b.transactionTime})

    setData(finalData)
  }, [tokenList])

  const sortArray = (blockTimestamp, tokenInfo) => {
    let balanceList = [];
    for (let i = 0; i < CHART_TICK_COUNT; i ++) {
      let from = blockTimestamp - (i + 1) * (timeKey[timeIndex].time / CHART_TICK_COUNT);
      let to = blockTimestamp - i * (timeKey[timeIndex].time / CHART_TICK_COUNT);
 
      if (i == 0) {
        balanceList[i] = {balance: tokenInfo.balance, price: 0}
      } else {
        balanceList[i] = {balance: balanceList[i - 1].balance, price: 0};
      }
      for (let j = 0; j < (tokenInfo.history || []).length; j ++) {
        if (tokenInfo.history[j].transactionTime >= from && tokenInfo.history[j].transactionTime < to)
        {
          let bal = (tokenInfo.history[j].isSent ? tokenInfo.history[j].amount : (tokenInfo.history[j].amount * -1))
          balanceList[i].balance += Number(bal);
        }
      }
      for (let j = 0; j < (tokenInfo.prices || []).length; j ++) {
        if (tokenInfo.prices[j].transactionTime >= from && tokenInfo.prices[j].transactionTime < to) {
          if (balanceList[i].price < tokenInfo.prices[j].quotePrice)
            balanceList[i].price = tokenInfo.prices[j].quotePrice;
        }
      }
      balanceList[i].transactionTime = to
      balanceList[i].decimals = tokenInfo.decimals;
    }

    for (let i = 0; i <= balanceList.length - 1; i ++) {
      if ((balanceList[balanceList.length - i - 1]?.price || 0) === 0)
        balanceList[balanceList.length - i - 1].price = (i === 0) ? balanceList[0].price  : balanceList[balanceList.length - i].price 
    }
    return balanceList
  }

  useEffect(() => {
    if (data.length > 0) {
      let min = 10000000;
      let max = 0;
      setChangePrice(
        (data[data.length - 1].balance - data[data.length - 2].balance).toFixed(2)
      );
      setChange(
        ((data[data.length - 1].balance || 1) / (data[data.length - 2].balance || 1) - 1).toFixed(
          3
        )
      );
      setCurrentPrice(data[data.length - 1].balance.toFixed(3));
      for (let i = 0; i < data.length; i++) {
        if (min > data[i].balance) min = data[i].balance;
        if (max < data[i].balance) max = data[i].balance;
      }
      setMinPrice(min - (max - min) / 2);
      setMaxPrice(max + (max - min) / 2);
    }
  }, [data]);

  const renderTooltip = (variable) => {
    let index = variable?.label;

    let payload;
    if (variable.payload && variable.payload.length > 0) {
      payload = variable.payload[0].payload
      setCurrentPrice(payload.balance.toFixed(3));
      if (index && index > 0) {
        setChangePrice(
          (data[index].balance - data[index -1].balance).toFixed(2)
        );
        setChange(
          ((data[index].balance || 1) / (data[index - 1].balance || 1) - 1).toFixed(
            3
          )
        );
      }
    }
    else {
      payload = {}
      if (data.length > 0) {
        setCurrentPrice(data[data.length - 1].balance.toFixed(3))
        if (index && index > 0) {
          setChangePrice(
            (data[data.length - 1].balance - data[data.length - 2].balance).toFixed(2)
          );
          setChange(
            ((data[data.length - 1].balance || 1) / (data[data.length - 2].balance || 1) - 1).toFixed(
              3
            )
          );
        }
      }
    }
    return (
      <Card className={classes.tooltipCard}>
        <Box style={{marginBottom: 6}}>
          {moment(new Date(payload.transactionTime * 1000)).format("DD/MM/YYYY HH:mm")}
        </Box>
        <Box className={classes.priceContainer}>
          <Box className={classes.dot}/>
          <Box>
            {payload.balance}
          </Box>
        </Box>
      </Card>
    )
  }

  return (
    <div className={classes.root}>
      <div className={classes.header}>Your Total Portfolio</div>
      <NormalCard>
        <Box className={classes.top_bar}>
          <Box className={classes.current_price}>${currentPrice}</Box>
          <Box className={classes.info} onClick={handleClick}>
            More info
            <InfoIcon className={classes.icon} />
          </Box>
        </Box>
        <Box
          className={
            changePrice > 0
              ? classes.change_plus
              : changePrice < 0
              ? classes.change_minus
              : classes.change_normal
          }>
          <Box className={classes.price}>
            {changePrice > 0 ? `+${changePrice}` : changePrice}
          </Box>
          <Box style={{ display: 'flex' }}>
            {changePrice > 0 ? (
              <ArrowDropUp className={classes.focusIcon}/>
            ) : changePrice < 0 ? (
              <ArrowDropDown className={classes.focusIcon}/>
            ) : (
              <></>
            )}
            <Box>{Math.abs(change)}%</Box>
          </Box>
        </Box>
        <div style={{ height: 300 }}>
          <ResponsiveContainer>
            <AreaChart
              width={500}
              height={400}
              data={data}
              margin={{
                left: 25,
                bottom: 10,
                top: 10,
                right: 25
              }}>
              <defs>
                <linearGradient id="colorUv" x1="0" y1="0" x2="0" y2="1">
                  <stop offset="0%" stopColor="#E0E3FF" stopOpacity={1} />
                  <stop offset="100%" stopColor="#E0E3FF" stopOpacity={0} />
                </linearGradient>
              </defs>
              <CartesianGrid vertical={false} height={10} />
              <XAxis hide />
              <YAxis
                axisLine={false}
                ticks={[
                  Math.floor((maxPrice - minPrice) / 4 * 0 + minPrice),
                  Math.floor((maxPrice - minPrice) / 4 * 1 + minPrice),
                  Math.floor((maxPrice - minPrice) / 4 * 2 + minPrice),
                  Math.floor((maxPrice - minPrice) / 4 * 3 + minPrice),
                  Math.ceil((maxPrice - minPrice) / 4 * 4 + minPrice),
                ]}
                tick={{stroke: 'red', strokeWidth: 0}}
                tickCount={5}
                interval={0}
                domain={[minPrice, maxPrice]}
              />
              <Tooltip content={renderTooltip}/>
              <Area
                type="monotone"
                dataKey="balance"
                stroke="#B2B7FF"
                fill="url(#colorUv)"
                activeDot={<CustomizedActiveDot />}
              />
            </AreaChart>
          </ResponsiveContainer>
        </div>
        <List
          className="tableScrollBar"
          style={{ display: 'flex', overflowX: 'auto', justifyContent: 'space-between', marginTop: 12 }}>
          {timeKey.map((item, index) => (
            <ListItem
              key={item.time}
              selected={timeIndex === index}
              className={classes.button}
              onClick={() => setTimeIndex(index)}>
              {item.title}
            </ListItem>
          ))}
        </List>
      </NormalCard>
      <Popover
        className={classes.popover}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left'
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right'
        }}>
        <Card className={classes.card}>
          <Box className={classes.main}>Your Total Portfolio</Box>
          <Box className={classes.sub}>
            Your total portfolio is a Fiat $ value representation of your total
            crypto holdings over time.
          </Box>
        </Card>
      </Popover>
    </div>
  );
};

export default Portfolio;
