import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { makeStyles, useTheme } from '@material-ui/styles';
import useStyles from './style';
import { ExpandCard, TableCard } from 'components';
import {
  Popover,
  Box,
  Card,
  TableCell,
  TableRow,
  TableHead,
  Table,
  TableContainer,
  Paper,
  TableBody,
  CircularProgress
} from '@material-ui/core';
import { InfoIcon } from 'assets/svgs/icon';
import NetworkInfo from '../../../../config/tokens';
import util from 'API-Service/util';
import { getBalanceWithMultiCall, getCorrectDecValue } from 'web3/contracts';
import { ETH_ADDRESS, MAIN_NETWORK_ID, WETH_ADDRESS } from 'utils/constants';
import { EmptyIcon } from '../Portfolio/icon/icon';

const TokenListInfo = props => {
  const { children, tokenList, loading } = props;
  const networkInfo = NetworkInfo[MAIN_NETWORK_ID]
  const theme = useTheme();
  const classes = useStyles(theme);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [data, setData] = useState([]);
  const open = Boolean(anchorEl);
  useEffect(() => {
    if (tokenList.length > 0) {
      getTokenList()
    }
  }, [tokenList])

  const getTokenList = async () => {
    const tokens = tokenList.map((item, index) => {return item.address === ETH_ADDRESS ? WETH_ADDRESS : item.address});
    let _tokenList = JSON.stringify(tokens);
    _tokenList = _tokenList.replace("\'", "\"");
    
    const _res = await util.getTokenListInfo(_tokenList);
    const tokenPriceInfo = _res.data.latestTokenData;
    const finalTokenInfoList = tokenPriceInfo.map((item, index) => {
      return {
        ...item,
        ...tokenList[index],
      }
    });
    setData(finalTokenInfoList)
  }

  function currencyFormat(num) {
    return '$' + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,');
  }
  const handleClick = event => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const renderImage = (row) => {
    let _list = networkInfo.tokens.filter((item) => { return item.address.toLowerCase() === row.address.toLowerCase()})
    return (
      _list.length > 0 ?
      <img
        src={ _list[0].logoURI}
        width="24"
        height="24"
      />
      :
      <EmptyIcon />
    );
  }

  return (
    <div className={classes.root}>
      <Box className={classes.top_bar}>
        <Box className={classes.header}>Your Crypto Wallet</Box>
        <Box className={classes.info} onClick={handleClick}>
          More info
          <InfoIcon className={classes.icon} />
        </Box>
      </Box>

      <ExpandCard className={classes.table} isProgress={loading}>
        <TableContainer
          style={{
            paddingBottom: data.length > 4 ? '47px' : '0px'
          }}
          className={classes.tableContainer + ' tableScrollBar'}>
          <Table>
            <TableHead className={classes.tableHeadBg}>
              <TableRow style={{ background: 'white' }}>
                <TableCell
                  style={{ position: 'sticky', left: 0 }}
                  className={classes.tableHead}>
                  Your Crypto
                </TableCell>
                <TableCell className={classes.tableHead}>Price</TableCell>
                <TableCell className={classes.tableHead}>Balance</TableCell>
                <TableCell className={classes.tableHead}>Value</TableCell>
                <TableCell className={classes.tableHead}>Change</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {data.sort((b, a) => {
                return a.latestPrice * getCorrectDecValue(a.balance, a.decimals, -1) - b.latestPrice * getCorrectDecValue(b.balance, b.decimals, -1)
              }).map(row => (
                <TableRow
                  key={row.name}
                  sx={{ '&:last-child td, &:last-child th': { border: 0 } }}>
                  <TableCell
                    className={classes.subTitle}
                    style={{
                      fontSize: 16,
                      position: 'sticky',
                      left: 0,
                      display: 'flex',
                      alignItems: 'center'
                    }}
                    component="th"
                    scope="row">
                    <div className={classes.logo}>
                      {
                        renderImage(row)
                      }
                    </div>

                    {row.name}
                  </TableCell>
                  <TableCell className={classes.subTitle}>
                    {currencyFormat(row.latestPrice)}
                  </TableCell>
                  <TableCell className={classes.subTitle}>
                    {`${getCorrectDecValue(row.balance, row.decimals, 4)} ${row.symbol}`}
                  </TableCell>
                  <TableCell className={classes.subTitle}>
                    {getCorrectDecValue(row.latestPrice * getCorrectDecValue(row.balance, row.decimals, -1), 0, 4)}
                  </TableCell>
                  <TableCell
                    style={{ width: '100px' }}
                    className={
                      Number(row.change) > 0
                        ? classes.success
                        : classes.danger
                    }>
                    {row.change > 0
                      ? '+' + getCorrectDecValue(row.change, 0, 4) + '%'
                      : getCorrectDecValue(row.change, 0, 4) + '%'}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
          {
            loading && <CircularProgress className={classes.loading}/>
          }
        </TableContainer>
      </ExpandCard>
      <Popover
        className={classes.popover}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left'
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'right'
        }}>
        <Card className={classes.card}>
          <Box className={classes.main}>Market Price</Box>
          <Box className={classes.sub}>
            The market price is the current price at which a crypto can be
            bought or sold.
          </Box>
          <Box className={classes.divide} />
          <Box className={classes.main}>Balance</Box>
          <Box className={classes.sub}>
            The balance reflects your current crypto holdings.
          </Box>
          <Box className={classes.divide} />
          <Box className={classes.main}>Value ($)</Box>
          <Box className={classes.sub}>
            The value in $ represents the total value of your crypto represented
            in Fiat. It is calculated as follows: Balance of your holdings x
            Market Price.
          </Box>
          <Box className={classes.divide} />
          <Box className={classes.main}>Change</Box>
          <Box className={classes.sub}>
            The percentage increase or decrease in a crypto asset.
          </Box>
        </Card>
      </Popover>
    </div>
  );
};

export default TokenListInfo;
