import React, { useState, useEffect } from 'react';
import {
  Portfolio,
  TokenListInfo,
  TrendingCrypto,
  TrendingNews
} from './components';
import { Grid } from '@material-ui/core';
import { useDispatch, useSelector } from 'react-redux';
import { ETH_ADDRESS, TRENDING_LIST } from 'utils/constants';
import { setTrendingData } from 'store/actions/App';
import TrendingEducations from './components/TrendingEducations';
import util from 'API-Service/util';
import { getBalanceWithMultiCall, getBlockTimestamp } from 'web3/contracts';
const timeKey = [
  {title: '1H', time: 3600},
  {title: '1D', time: 3600 * 24},
  {title: '1W', time: 3600 * 24 * 7},
  {title: '1M', time: 3600 * 24 * 30},
  {title: '1Y', time: 3600 * 24 * 365},
  {title: 'ALL', time: 3600 * 24 * 365 * 10}
]

const Dashboard = props => {
  const account = useSelector(state => state.app.currentWallet);
  // const account = "0xeB6c4bE4b92a52e969F4bF405025D997703D5383"
  const trendingData = useSelector(state => state.app.trendingData);
  const [tokenList, setTokenList] = useState([]);
  const [timeIndex, setTimeIndex] = useState(0);
  const [loading, setLoading] = useState(false);
  const [blockTime, setBlockTime] = useState(0);
  const [walletTokenList, setWalletTokenList] = useState([]);
  const dispatch = useDispatch();

  useEffect(() => {
    fetchTrendingBitqueryData();
  }, []);

  useEffect(() => {
    if (account && blockTime != 0)
      getTokenBalance();
  }, [account, timeIndex, blockTime])

  useEffect(() => {
    if (account && blockTime != 0)
      getTokenList();
  }, [account, blockTime])

  const getTokenBalance = async () => {
    const res = await util.getTokenBalanceListFromWallet(account,  blockTime - timeKey[timeIndex].time)
    let tokenInfoList = res.data.transferData.map((item, index) => { return {address: item.tokenAddress, name: item.name, symbol: item.symbol, decimals: item.decimals, history: item.history}});
    tokenInfoList = [{address: ETH_ADDRESS, name: 'Ethereum', symbol: 'ETH', decimals: 18}, ...tokenInfoList];

    const balanceList = await getBalanceWithMultiCall(tokenInfoList, account);
    
    const tokenListInfo = tokenInfoList.map((item, index) => {
      return {
        ...item,
        balance: balanceList.data[index]
      }
    })
    setTokenList(tokenListInfo);

  }

  const getTokenList = async () => {
    setLoading(true);
    const res = await util.getWalletTokenData(account)
    console.log(res)
    let tokenInfoList = res.data.walletTokenData.map((item, index) => { return {address: item.tokenAddress, name: item.name, symbol: item.symbol, decimals: item.decimals, history: item.history}});
    tokenInfoList = [{address: ETH_ADDRESS, name: 'Ethereum', symbol: 'ETH', decimals: 18}, ...tokenInfoList];
    const balanceList = await getBalanceWithMultiCall(tokenInfoList, account);
    const tokenListInfo = tokenInfoList.map((item, index) => {
      return {
        ...item,
        balance: balanceList.data[index]
      }
    })
    setWalletTokenList(tokenListInfo);
    setLoading(false);
  }

  const fetchTrendingBitqueryData = async () => {
    const blockTimestamp = await getBlockTimestamp();
    setBlockTime(blockTimestamp);
    const data = await Promise.all(
      TRENDING_LIST.map(async cell => {
        const queryResult = await util.getTrendingCrypto(cell.network, cell.address, cell.quoter)
        const { dexTrades } = queryResult.data.ethereum;
        let item = {};
        item.symbol = cell.symbol;
        if (dexTrades && dexTrades.length > 0)
          item.price = '$' + parseFloat(dexTrades[0].close_price).toFixed(2);
        else item.price = '$0';
        item.logo = cell.logo;
        return item;
      })
    );

    dispatch(setTrendingData(data));
    localStorage.setItem('trendingData', JSON.stringify(data));
  };
  return (
    <>
      <TokenListInfo tokenList={walletTokenList} loading={loading}/>
      <Grid container spacing={2}>
        <Grid item xs={12} md={7}>
          <Portfolio tokenList={tokenList} timeIndex={timeIndex} setTimeIndex={setTimeIndex} blockTime={blockTime}/>
        </Grid>
        <Grid item xs={12} md={5}>
          <TrendingCrypto data={[...trendingData]} />
        </Grid>
        <Grid item xs={12}>
          <TrendingNews />
        </Grid>
        <Grid item xs={12}>
          <TrendingEducations />
        </Grid>
      </Grid>
    </>
  );
};

export default Dashboard;
