import React, { useState, useEffect } from 'react';
import { Box, Grid } from '@material-ui/core';
import { useTheme } from '@material-ui/styles';
import useStyles from './style';
import parse from 'rss-to-json';
import { RandomImages } from 'utils/services';
import { ImageCard } from 'components';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/swiper.min.css';
import useWindowSize from 'shared/hooks/useWindowSize';

const TrendingNews = props => {
  const { children } = props;
  const theme = useTheme();
  const classes = useStyles(theme);
  const [rssItems, setRSSItems] = useState([]);
  const size = useWindowSize();
  useEffect(() => {
    const fetchPosts = async () => {
      const url =
        'https://s3.amazonaws.com/revue/accounts/rss_feeds/000/298/540/original/rss_feed_298540.xml?format=rss';
      const feeds = await parse(url, { method: 'get' });
      if (feeds && feeds.items.length > 0) {
        setRSSItems(feeds.items.slice(0, 4));
      }
    };
    fetchPosts();
  }, []);
  const cardWidth = size.width > 992 ? 375 : 300;
  return (
    <Box className={classes.root}>
      <Box className={classes.header}>Trending News</Box>
      <Grid
        style={{ maxWidth: size.width }}
        className={classes.content}
        container
        spacing={5}>
        <Swiper
          slidesPerView={
            size.width / cardWidth > 4 ? 4 : size.width / cardWidth
          }
          onSlideChange={() => {}}
          onSwiper={swiper => {}}
          pagination={{ clickable: true }}
          style={{ margin: 0, padding: 10 }}>
          {rssItems.map((item, index) => {
            const image = RandomImages();
            return (
              <SwiperSlide key={index} style={{ padding: 10, paddingBottom: 0 }}>
                <ImageCard
                  key={item.length && item.created}
                  title={item.title}
                  image={image}
                  text={item.description.$text}
                  href={item.link}
                />
              </SwiperSlide>
            );
          })}
        </Swiper>
      </Grid>
    </Box>
  );
};

export default TrendingNews;
